import styles from "./Team.module.css";

function Team() {
  const [hover, setHover] = useState(false);
  return (
    <>
      <div className={styles.parent} onMouseLeave={() => setHover(false)}>
        {[1, 2, 3, 4, 5].map((x, index) => (
          <div
            className={styles.team}
            style={
              hover
                ? {
                    left: `calc(50% + ${25 * Math.sin((index * 3.1415926 * 4) / 5)}rem)`,
                    top: `calc(50% - ${20 * Math.cos((index * 3.1415926 * 4) / 5)}rem)`,
                  }
                : { left: "50%" }
            }
          >
            <div style={{ width: "100%", margin: "0", padding: "0" }}>
              <img src={team3} alt="" className={styles.pfp} />
            </div>
            <h1>Mitsuha Miyamizu</h1>
          </div>
        ))}
        {/*eslint-disable*/}
        <div
          className={styles.card}
          onMouseEnter={() => {
            setHover(true);
          }}
          onClick={() => setHover(true)}
        >
          <h1>Technical Team</h1>
          <hr />
          <p>The technical team is the beshht!</p>
        </div>
      </div>
      )
    </>
  );
}

export default Team;
